

@import url('https://fonts.googleapis.com/css2?family=Pontano+Sans&display=swap');

body {
  margin: 0;
  font-family: 'Pontano Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Below are the general styles */

body{
  margin-left:3rem;
  padding-top:2rem;
  background-color:#17172C;
}

h1,
h2{
  font-size:1.3rem;
}

p{font-size: 1.2rem;
  margin-bottom:0rem;
  margin-top:0.5rem;
}


.firstpage{
  display: flex;
}

.rebigcontainer{
  width:15rem;
}


/* styles for all headings */

.reheading,
.heading,
.heading_news{
  background-color:#2F1F51;
  color:white;
  /* border:1px solid white; */
  border-radius:5px;
  text-align: center;
  width:13rem;
  padding:0.3rem;
  display: block;
  margin:auto;
  margin-bottom:0.5rem;
}

/* different headings have different widths */

.heading{
  width:10rem;
}

.heading_news{
  width:12rem;
}
/* below are the styles for recommendations */


.recontainer {
  border-radius:1rem;
  width: 15rem;
  height: 40rem;
  position: fixed;
  overflow: auto;
}

.recompany{
  display: flex;
  flex-wrap: wrap;
  width:100%;
}

.symbolntime{
  display: flex;
  justify-content: space-between;
  margin-bottom:-0.5rem;
}

.recompanyinfo h2{
  font-weight: 600;;
}

.recompanyinfo{
  background-color: #ebecf1;
  padding-left:1rem;
  padding-right:1rem;
  padding-bottom:1rem;
  border-bottom:1px solid black;
}

/* mainbox */

.mainbox{
  height:100%;
  margin-left:1rem;
}


.bigcontainer{
  width:15rem;
}

.threestocks {
  display:flex;
}

.container{
  border-radius:1rem;
  width: 15rem;
  height: 17rem;
  position: fixed;
  overflow: auto;
}

.company{
  display: flex;
  flex-wrap: wrap;
  margin-left:0.5rem;
  margin-right:0.5rem;
}

.companyinfo h2{
  border-bottom:1px solid green;
  padding-bottom:0.5rem;
}

.companyinfo{
  background-color: #ebecf1;
  border-bottom:1px solid black;
  margin-bottom:1rem;
  padding-bottom:1rem;
  width:100%;
  padding-left:1rem;
  padding-right:1rem;
}


.logo {
  padding:0.5rem;
  border-radius:3rem;
  position:relative;
  top:4rem;
  left:4rem;
  width:7rem;
  animation: logo 10s infinite linear;
}

/* below tech news articles */


.techcrypto {
  display: flex;
  margin-top:10rem;
  
}

.techcrypto div{
  margin-right:0.5rem;;
  width:20.1rem;
}

.technews{
  display: flex;
  height:21rem;
  border-radius:1rem;
  position: fixed;
  overflow: auto;
  margin-left:0.5rem;
  margin-right:0.5rem;
}

.techarticle{
  display: flex;
  flex-wrap: wrap;
  background-color:blue;
}

.articleinfo{
  background-color: #ebecf1;
  border-bottom:1px solid black;
  padding-left:1rem;
  padding-right:1rem;
  padding-bottom:1rem;
  margin-bottom:1rem;
}

.articleheading{
  width:18rem;
  border-bottom:1px solid green;
  padding-bottom:0.5rem;
}

.articleinfo .image{
  height:8rem;
  object-fit: contain;
}

.articleinfo .summary{
  width:18.1rem;
}

/* second page */

.firstpage{
  display:none;
}

.secondpage{
  padding:0rem;
  background-color:white;
  margin-left:-3rem;   /* because body has margin-left:3rem */
}


/*sector styles */


.sector{
    display: flex;
    height:45rem;
    border-radius:1rem;
    position: fixed;
    overflow: auto;
    margin-left:0.5rem;
    margin-right:0.5rem;
}

.sectorarticle{
  display: flex;
  flex-wrap: wrap;
  margin-left:21rem;
}

.secarticleinfo{
  background-color: #ebecf1;
  border-bottom:1px solid black;
  padding-left:1rem;
  padding-right:1rem;
  padding-bottom:1rem;
  margin-bottom:1rem;
  width:80%;
  border-radius:1rem;
}

.secarticleinfo .image{
  height:8rem;
  object-fit: contain;
}

.sectorstocksbox{
  background-color:pink;
  margin-left:2rem;
  margin-top:-1rem;
  padding:1rem;
  border-radius:1rem;
  height:45rem;
  width:14rem;
  position: fixed;
  overflow: auto;
}



